<template>
  <input
    id="uploader"
    :multiple="multiple ? 'multiple' : false"
    type="file"
    name="image"
    accept="image/png, image/jpeg, image/gif"
    style="display: none"
    @change="handleUpload">
</template>

<script>
import vpd from '../mixins/vpd'
export default {
  name: 'VpdUploader',
  mixins: [vpd],
  props: {
    upload: Function,
    uploadOption: Object
  },
  data () {
    return {
      multiple: false, // 
      uploader: null, // input file
      cb: null // 
    }
  },

  computed: {
    // 
    top () {
      return this.$vpd.state.top
    }
  },

  mounted () {
    this.uploader = document.getElementById('uploader')

    /**
     * 
     * 
     *
     * 
     * this.$vpd.$emit('upload', function (payload) {/.../})
     * @param payload { Array } 
     * @param multiple { Boolean }  false
     */
    this.$vpd.$on('upload', (cb, multiple) => {
      this.multiple = !!multiple
      this.cb = cb
      setTimeout(() => {
        this.uploader.click()
      }, 0)
    })
  },
  beforeDestroy () {
    this.$vpd.$off('upload')
  },
  methods: {
    // change
    handleUpload () {
      var files = this.uploader.files

      if (!files || files.length === 0) return

      files = Array.prototype.slice.call(this.uploader.files)

      let uploadFn = this.defaultUpload

      uploadFn(files).then(res => {
        if (!res.ok) {
          return
        }
        res.text().then((data) => {
          var pData = JSON.parse(data)

          // 
          new Promise(resolve => {
            const imgUrl = process.env.BASE_API + pData.imgUrl
            this.handleLoadQueue(resolve, imgUrl)()
          }).then(payload => {
            this.cb(payload)
          })
        })
      })
    },

    defaultUpload (files) {
      if (this.uploadOption.url) {
        var data = new FormData()
        files.forEach(file => data.append('file', file))

        return fetch(this.uploadOption.url, {
          method: 'POST',
          body: data
        })
      } else {
        alert('api')
      }
    },

    /**
     * 
     * 
     */
    handleLoadQueue (resolve, url) {
      var payload = []

      var download = () => {
        new Promise(resolve => {
          this.getImageWidth(url, resolve)
        }).then(size => {
          payload.push({
            width: size.w,
            height: size.h,
            top: this.top,
            url: url, // 
            src: url // 
          })

          // 
          resolve(payload)
        })
      }

      return download
    },

    /**
     *  new Image 
     *  promise
     *
     * @param url { URL | base64 }  url
     * @param res { Promise resolve }
     *
     * @return { Object } 
     */
    getImageWidth (url, res) {
      var img = new Image()
      img.src = url
      img.onload = function () {
        res({
          w: Math.round(img.width),
          h: Math.round(img.height)
        })
      }
    }
  }
}
</script>
